import { useState, useEffect } from 'react';

// material
import { Box, Container, Typography, styled, Paper, Grid, Divider, Button, Stack } from '@material-ui/core';

import { motion } from 'framer-motion';
import { useTheme } from '@material-ui/core/styles';

import { useEthers, useCall, useContractFunction } from '@usedapp/core';
import { utils, Contract } from 'ethers';

// hooks
import useSettings from '../../hooks/useSettings';

import { varFadeIn, varFadeInUp, varWrapEnter, varFadeInRight } from '../animate';
import {
  MERKLE_DISTRIBUTER_2_ADDRESS,
  MERKLE_DISTRIBUTER_2_CONTRACT,
  MERLKE_DISTRIBUTER_INTERFACE,
  SNAPSHOT_BLOCK
} from '../../config';
import useClaims from '../../hooks/useClaims';

// ----------------------------------------------------------------------

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(2),
  color: theme.palette.text.primary,
  height: '100%'
}));

const AnimateContainer = styled(motion.div)(({ theme }) => ({}));

export default function XCdropBlock() {
  const { account } = useEthers();

  const data = useClaims('2');

  const [claimValue, setClaimValue] = useState('0');
  const [errorMessage, setErrorMessage] = useState('');
  const [isClaiming, setIsClaiming] = useState(false);

  const claimed = useCall({
    contract: new Contract(MERKLE_DISTRIBUTER_2_ADDRESS, MERLKE_DISTRIBUTER_INTERFACE),
    method: 'claimed',
    args: [account]
  });

  useEffect(() => {
    if (data && data.amount !== '' && claimed && claimed.value) {
      let amount = utils.parseEther(utils.formatEther(data.amount.toString()));

      if (amount.gte(claimed.value[0])) {
        amount = amount.sub(claimed.value[0]);
      }

      setClaimValue(utils.formatEther(amount));
    }
  }, [data, claimed, account]);

  const { state, send } = useContractFunction(MERKLE_DISTRIBUTER_2_CONTRACT, 'claim', {
    transactionName: 'Claim JUICE'
  });

  const handleClaimSubmit = () => {
    setIsClaiming(true);
    send(data.index, account, data.amount, data.proof);
  };

  useEffect(() => {
    if (state.status === 'Exception') {
      setIsClaiming(false);
      setErrorMessage(state.errorMessage);
    }

    if (state.status === 'Exception' || state.status === 'Fail') {
      setIsClaiming(false);
      setErrorMessage(state.errorMessage);
    }

    if (state.status === 'None') {
      setIsClaiming(false);
      setErrorMessage('');
    }

    if (state.status === 'Success') {
      setIsClaiming(false);
      setErrorMessage('');
    }
  }, [state, account]);

  return (
    <>
      <Item
        style={{
          display: 'flex',
          flexDirection: 'column',
          border: '0.25rem solid #35C4EF',
          minHeight: '16rem',
          height: 'fit-content',
          backgroundImage: 'url(/static/claimjuice/claim-juice-bg-3.jpg)',
          backgroundPosition: 'center',
          backgroundSize: 'cover',
          marginTop: '1rem'
        }}
      >
        <Typography variant="h4" style={{ marginBottom: '0.4rem' }}>
          Claim <span style={{ color: '#ffc900' }}>$JUICE</span> from <span style={{ color: '#35C4EF' }}>$XDROPS</span>
        </Typography>
        <Typography style={{ marginTop: '-0.4rem', opacity: '0.6', marginBottom: '0.4rem' }}>
          Claim the $JUICE from your $CDROP staking.
        </Typography>

        <Divider />

        <Typography style={{ marginTop: '1.4rem' }}>Available to claim:</Typography>
        <Stack direction="row" alignItems="center" style={{ flexWrap: 'wrap' }}>
          <Typography variant="h3">{claimValue} $JUICE </Typography>
          <img src="/static/claimjuice/JuiceLogo.svg" alt="" style={{ width: '2rem' }} />
        </Stack>

        <Button
          color="yellow"
          size="large"
          variant="contained"
          style={{ marginTop: '4rem' }}
          onClick={() => {
            handleClaimSubmit();
          }}
          disabled={isClaiming || utils.parseEther(claimValue).eq('0')}
        >
          {isClaiming ? <>CLAIMING ...</> : <>CLAIM</>}
        </Button>
      </Item>
    </>
  );
}
