import { useEthers } from '@usedapp/core';
import { useCallback, useEffect, useState } from 'react';
import { WORKER_HOST } from '../config';

const useClaims = (proofsNumber) => {
  const [data, setData] = useState({
    index: 0,
    amount: '',
    proof: []
  });

  const { account } = useEthers();

  const fetchClaims = useCallback(async ()=> {
    if (account) {
      try {
        const response = await fetch(WORKER_HOST, {
          // body: JSON.stringify({ 'chainId':chainId, 'account': account }),
          body: JSON.stringify({ key: `${proofsNumber}:${account}` }),
          headers: {
            'Content-Type': 'application/json',
            'Referrer-Policy': 'no-referrer'
          },
          method: 'POST'
        });

        const result = await response.json();

        setData(result);
      } catch {
        setData({
          index: 0,
          amount: '0',
          proof: []
        });
      }
    }
  }, [account]);

  useEffect(() => {
    fetchClaims();
  }, [account, fetchClaims]);

  return data;
};

export default useClaims;
